import React, { Component } from 'react';
import styled, {keyframes} from "styled-components";
const windowWidth = window.innerWidth;
const windowHeight = window.innerHeight;




const MainWrapper = styled.div`
    height : ${props => (props.height)};
    width : ${props => (props.width)};
`
const HomeTop = styled.div`
    display : flex;
    align-items: center;
    width:100%;
    height:20%;
`
const HomeCenter = styled.div`
    width:100%;
    height:80%;
    position: relative;
`
const HomeRight = styled.div`
    display : flex;
    align-items: center;
    justify-content:center;
    width:30%;
    height:50%;
    position: absolute;
    right:0;
    bottom:0;
`
const HomeMonitor = styled.div`
    display : flex;
    width:80%;
    height:100%;
    position: absolute;
    justify-content:center;
    align-items:center;
`
const HomeMonitorItem = styled.div`
    display : flex;
    justify-content:center;
    align-items:center;
    width:100%;
    height:100%;
    position: absolute;
`
const MonitorItemAnimation = keyframes`
  from {
    margin-right: 100%;
  }

  to {
    margin-right: 0%;
  }
`

const  MonitorItem = styled.a`
`

const TopItemAnimation = keyframes`
  from {
    margin-top: -10%;
  }

  to {
    margin-top: 0%;
  }
`

const TopItem = styled.a`
  animation : ${TopItemAnimation} 1s;
`
const RightItemAnimation = keyframes`
  from {
    margin-left: 100%;
  }

  to {
    margin-left: 0%;
  }
`

const RightItem = styled.div`
  display : flex;
  justify-content : center;
  align-items : center;
  height :100%;
  width :80%;
  border-radius : 3px;
  animation : ${RightItemAnimation} 1s;
    background-color : #d9d9d9;
`
const PFImg = styled.img`
    width : 100%;
    height : 100%;
    border-radius : 20px;
   animation : ${MonitorItemAnimation} 1s;
`
const LogoContainer = styled.div `
  display: flex;
  flex-direction : row;
  align-items : center;
  width : 50%;
  height :20%;
`
const LogoItem = styled.img `
  height : 50%;
  width : height;
`

class MainContainer extends Component {
  constructor(props){
    super()
    this.state={
      ...props,
      wrapperWidth : windowWidth/2 +"px",
      wrapperHeight : windowHeight/2 +"px",
      PFImg : require("../img/drpayapp.png"),
      PFTitle : "급여박사2",
      LogoImgList :[{Title : "급여박사2", Img : require("../img/drpayapp.png"),imgLotation : require("../img/logo_drpayapp.png")}],
      
    }
  }
  _LogoItemClick=(idx)=>{
      this.setState({
        PFImg : this.state.LogoImgList[idx].Img,
        PFTitle : this.state.LogoImgList[idx].Title
      })
  }
  updateWindowDimensions=() =>{
    this.setState({ wrapperWidth: window.innerWidth/2 +"px", wrapperHeight: window.innerHeight/2 +"px" });
  }
  componentDidMount(){
    window.addEventListener('resize', this.updateWindowDimensions);
  }
  render() {
    return (  
        <MainWrapper width={this.state.wrapperWidth} height={this.state.wrapperHeight}>     
          {/* TOP */}
          <LogoContainer>
            <LogoItem src = {this.state.LogoImgList[0].imgLotation} onClick = {()=>{this._LogoItemClick(0)}}></LogoItem>
          </LogoContainer>
          <HomeTop>
            <TopItem>
              <h1>{this.state.PFTitle}</h1>
            </TopItem>
          </HomeTop>
          <HomeCenter>     
          {/* COMPUTER */}
          <HomeMonitor>
            <HomeMonitorItem>
              <PFImg src = {this.state.PFImg}></PFImg>
            </HomeMonitorItem>
          </HomeMonitor>
          {/* RIGHT */}       
            <HomeRight>
              <RightItem>
                <h2>REACT NATIVE</h2>
              </RightItem>
            </HomeRight>
          </HomeCenter>
        </MainWrapper>
    );
  }
}

export default MainContainer;
