import React from 'react';
import styled from 'styled-components'
import MyPicImg from '../img/MyPic.png'
const ProfileMainContainer = styled.div`
    border : 1px solid #fff;
    display : flex;
    flex-direction: column;
    width : 50%;
    padding-left : 5%;
    padding-right : 5%;
    @media ( max-width: 480px ) {
        width: 100%;
    }
`
const ProfileTopContainer = styled.div`
    display :flex;
    width :100%;
    justify-items : center;
    flex-direction : column;
    @media ( max-width: 480px ) {
        flex-direction : column;
    }
`
const ProfileTopSubContainer = styled.div`
    display :flex;
    flex : 8;
    flex-direction : column;
    height : 100%;
    @media ( max-width: 480px ) {
        width: 100%;
        margin-top : 10%;
        margin-left : 0;
    }
`
const ProfileTopSubItem = styled.div`
    display :flex;
    flex:1;
    justify-items : center;
    align-items :center;
    flex-direction : row;
`
const MyPic = styled.img`
    width :100px;
    height : 130px;
    border-radius : 20px;
    margin : 0 0 3% 10% ;
`
const ProfileTopSubItemDetail = styled.a`
    flex:1;
    text-align :left;
`
const ProfileBottomContainer = styled.div`
    display :flex;
    width :100%;
    flex-direction : column;
    @media ( max-width: 480px ) {
    flex-direction : column;
    }
`
const PrifileTitle = styled.h1`
    margin : 10% 0 5% 0 ;
`
const PrifileNote = styled.h3`
    margin : 3% 0 0 10% ;
    flex:1;
    @media ( max-width: 480px ) {
        flex-direction : column;
        font-size: 18px;
    }
`
const PrifileCompany = styled.h3`
    margin : 3% 0 0 10% ;
    flex:2;
    @media ( max-width: 480px ) {
        flex-direction : column;
        font-size: 18px;
    }
`
const PrifileNoteContainer = styled.div`
    display :flex;
    flex-direction : row;
    @media ( max-width: 480px ) {
        flex-direction : column;
    }
`
const PrifileNoteDetail = styled.h3`
    margin : 1% 0 3% 13% ;
    @media ( max-width: 480px ) {
        flex-direction : column;
        font-size: 18px;
    }
`
const Profile = () => {
    return (
        <ProfileMainContainer>
            <PrifileTitle>PROFESSOR</PrifileTitle>
            <ProfileTopContainer>
                <MyPic src ={MyPicImg}>
                </MyPic>
                <ProfileTopSubContainer>
                    <PrifileNote>이　　름 　김종우</PrifileNote>
                    <PrifileNote>생년월일 　1991.08.15</PrifileNote>
                    <PrifileNote>메일주소 　naace77@naver.com</PrifileNote>
                    <PrifileNote>전화번호 　010-8360-3806</PrifileNote>
                    {/* <ProfileTopSubItem>
                        <ProfileTopSubItemDetail>이름</ProfileTopSubItemDetail>
                        <ProfileTopSubItemDetail>김종우</ProfileTopSubItemDetail>
                    </ProfileTopSubItem>
                    
                    <ProfileTopSubItem>
                        <ProfileTopSubItemDetail>생년월일</ProfileTopSubItemDetail>
                        <ProfileTopSubItemDetail>1991.08.15</ProfileTopSubItemDetail>
                    </ProfileTopSubItem>
                    
                    <ProfileTopSubItem>
                        <ProfileTopSubItemDetail>EMAIL</ProfileTopSubItemDetail>
                        <ProfileTopSubItemDetail>naace77@naver.com</ProfileTopSubItemDetail>
                    </ProfileTopSubItem>
                    
                    <ProfileTopSubItem>
                        <ProfileTopSubItemDetail>TEL</ProfileTopSubItemDetail>
                        <ProfileTopSubItemDetail>010-8360-3806</ProfileTopSubItemDetail>
                    </ProfileTopSubItem> */}
                </ProfileTopSubContainer>
            </ProfileTopContainer>
            <ProfileBottomContainer>
            <PrifileTitle>EDUCATION</PrifileTitle>
            <PrifileNote>2010.02 　청주 상당고등학교</PrifileNote>
            <PrifileNote>2019.02 　청주 대학교</PrifileNote>
            <PrifileTitle>CAREER</PrifileTitle>
            
            <PrifileNoteContainer>
            <PrifileNote>2011.04 ~ 2012.02</PrifileNote>
            <PrifileCompany>국방부 해병대</PrifileCompany>
            </PrifileNoteContainer>
            <PrifileNoteDetail>- 해병대 (병) 1139기</PrifileNoteDetail>  
            <PrifileNoteContainer>
            <PrifileNote>2012.02 ~ 2016.02</PrifileNote>
            <PrifileCompany>국방부 해병대</PrifileCompany>
            </PrifileNoteContainer>
            <PrifileNoteDetail>- 해병대 (부) 333기</PrifileNoteDetail>  
            <PrifileNoteContainer>
            <PrifileNote>2016.07 ~ 2017.01</PrifileNote>
            <PrifileCompany>(주)뉴한시스</PrifileCompany>
            </PrifileNoteContainer>
            <PrifileNoteDetail>- Android App 개발 아르바이트 <a className="useLang">(Java, Android)</a></PrifileNoteDetail>   
            <PrifileNoteContainer>
            <PrifileNote>2017.12 ~ 2018.04</PrifileNote>
            <PrifileCompany>(주)디엘정보기술_본사</PrifileCompany>
            </PrifileNoteContainer>
            <PrifileNoteDetail>- MES, MRP 유지보수 <a className="useLang">(VB6, C#, MySql)</a></PrifileNoteDetail>            
            <PrifileNoteContainer>
            <PrifileNote>2018.04 ~ 2018.08</PrifileNote>
            <PrifileCompany>(주)디엘정보기술_매그나칩 반도체</PrifileCompany>
            </PrifileNoteContainer>
            <PrifileNoteDetail>- 자동화 UI 유지보수, 신규개발 <a className="useLang">(C#, Oracle)</a></PrifileNoteDetail>                    
            <PrifileNoteContainer>
            <PrifileNote>2018.04 ~</PrifileNote>
            <PrifileCompany>(주)라인페이롤서비스</PrifileCompany>
            </PrifileNoteContainer>
            <PrifileNoteDetail>- 급여박사2 하이브리드 앱 개발 <a className="useLang">(ReactNative)</a></PrifileNoteDetail>   
            
            </ProfileBottomContainer>
        </ProfileMainContainer>
    );
};

export default Profile;